.app-navbar-brand {
	padding: (@line-height-computed / 3) 0 0 @line-height-computed;
	color: #000;
    font-size: 1.5rem;
}

.app-navbar-brand-image {	
	padding: 0;
	float: left;
}

.app-brandname-logo {
	width: 100%;
}

.app-navbar {
	border-top: 0px solid @app-gray;
	border-bottom: 0;
	background: transparent;
}

@media(min-width:767px) {
	.app-navbar {
		float: right;
		padding: 40px 0;
		-webkit-transition: background .5s ease-in-out,padding .5s ease-in-out;
		-moz-transition: background .5s ease-in-out,padding .5s ease-in-out;
		transition: background .5s ease-in-out,padding .5s ease-in-out;
		a {
    		color: #fff !important;
		}
	}
	.app-navbar-collapse {
		padding: 20px 0 10px 0;
		background: #000;
		a {
			color: #777 !important;
		}
	}
}

.navbar-nav {
	float: right !important;
}

.navbar-nav > li > a {
    padding: (@line-height-computed / 4) (@line-height-computed / 2) (@line-height-computed / 2) (@line-height-computed / 2);
    text-transform: uppercase;    
}

.navbar-nav > li > a:focus {
    color: @app-danger !important;
}